<template>
  <div>

    <div v-show="step">
    <pre class="prettyprint lang-js">
      <code>
    function createVNode () {
      return h(this.tag, this.data, this.children)
    }

    function updateObj (oldv, data) {
      return patch(oldv, createVNode.call(data))
    }
</code>
    </pre>
    </div>

  <div v-show="step">
    <pre class="prettyprint lang-js">
      <code>
      var obj = {
      tag: 'button',
      data: {
        type: 'button',
        class: { 'btn': true, 'btn-green': true },
        on: {
          click: function () {
            window.open('https://vuejs.org/v2/guide/render-function.html#ad','_blank');
          }
        }
      },
      children: 'To Vue Blog'
    }</code>
    </pre>

  </div>
  </div>
</template>
<script>

  const snabbdom = require('snabbdom/snabbdom.js');
  import h from 'snabbdom/h';
  const patch = snabbdom.init([
    require('snabbdom/modules/class').default,
    require('snabbdom/modules/props').default,
    require('snabbdom/modules/style').default,
    require('snabbdom/modules/eventlisteners').default
  ]);


    var handler;
    var oldv = document.getElementById('static_dom');

    var vnode = h('div#static_dom', {
      class: { 'classed' : true },
      style: { color: '#f00',
      textDecoration: 'underline'},
      on: {click:changeToButton}
    }, 'but changed by virtual dom');

    setTimeout(function () {
      oldv = patch(oldv, vnode);
      console.log(oldv);
    }, 3000)

    function changeToButton(){
      oldv = updateObj(oldv, obj);
      typeof handler ==='function' && handler();
    }


    var obj = {
      tag: 'button',
      data: {
        type: 'button',
        class: { 'btn': true, 'btn-green': true },
        on: {
          click: function () {
            window.open('https://vuejs.org/v2/guide/render-function.html#ad','_blank');
          }
        }
      },
      children: 'To Vue Blog'
    }

    function updateObj (oldv, data) {
      return patch(oldv, createVNode.call(data))
    }


    function createVNode () {
      return h(this.tag, this.data, this.children)
    }
  export default {
    data(){
      return {
        a:0,
        b:0,
        step: false
      }
    },
    computed: {
      c () {
        ++this.b;
        return this.a
      }
    },
    methods: {
      changeStep(){
        this.step = true;
      },
      increase (){
        ++this.a
      },
      callPretty (vm){
        if(window.PR) {
          window.PR.prettyPrint();
        } else {
          setTimeout(function(){vm.callPretty(vm)},500);
        }
      }
    },
    created(){
      handler = this.changeStep.bind(this);
      window.inc=this.increase.bind(this);
    },
    mounted(){
      this.callPretty(this);
    }
  }
</script>
<style>
 html pre.prettyprint {
    padding: 0 20px ;
    border-color: #eee;
    line-height: 1.7em;
   font-size: 20px;
   font-weight: bold;
  }
</style>


